"use client";

import { useEffect, useState } from "react";
import { useParams } from "next/navigation"; // Importa useParams
import Image from "next/image";
import { supabase } from "@/utils/supabaseClient";

export default function ProductDetail() {
  const { id } = useParams(); // Obtén el ID de la URL
  const [product, setProduct] = useState(null);

  // Función para cargar el producto desde Supabase
  const fetchProduct = async () => {
    if (!id) return; // Asegúrate de que el id esté definido
    try {
      const { data, error } = await supabase
        .from("products")
        .select("*")
        .eq("id", id)
        .single(); // Obtén solo un producto

      if (error) throw error;
      setProduct(data);
    } catch (error) {
      console.error("Error al obtener el producto:", error);
    }
  };

  useEffect(() => {
    fetchProduct();
  }, [id]);

  if (!product) {
    return <p>Cargando producto...</p>;
  }

  return (
    <div className="max-w-4xl mx-auto py-8 px-4">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
        {/* Imagen del producto */}
        <div>
          <Image
            src={product.image_url}
            alt={product.name}
            width={500}
            height={500}
            className="rounded-lg object-cover"
          />
        </div>
        {/* Detalles del producto */}
        <div>
          <h1 className="text-2xl font-bold mb-4">{product.name}</h1>
          <p className="text-gray-600 mb-4">{product.description}</p>
          <span className="text-xl font-semibold text-black mb-6">
            ${product.price}
          </span>
          <div className="mt-6">
            <label className="block mb-2 font-medium">Intensidad:</label>
            <div className="flex gap-1">
              {Array.from({ length: 5 }).map((_, index) => (
                <span
                  key={index}
                  className={`w-4 h-4 rounded-full ${
                    index < product.intensity ? "bg-black" : "bg-gray-300"
                  }`}
                ></span>
              ))}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
